.ui-menu {
  max-width: 100%;
}
.ui-menu .ui-menu-item {
  border-bottom: 1px solid #eee;
  font-size: 20px;
  height: 50px;
  color: #888;
}
.reader {
  height: 100%;
}
.storia {
  margin-top: 0px;
  font-size: 4.5vw;
  line-height: 25px;
  -webkit-transition: margin 0.2s ease;
  transition: margin 0.2s ease;
  padding-top: 25px;
}
.storia span {
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
  padding-bottom: 30px;
  margin-left: 10px;
  display: inline-block;
}
.storia span.active {
  opacity: 1 !important;
}
.storia .light {
  opacity: 1;
}
.header {
  z-index: 2;
  background-color: #fff;
}
.footer {
  z-index: 2;
  background-color: #fff;
  bottom: 0px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: fixed;
  width: 100%;
  border-top: 1px solid #eee;
  padding-top: 10px;
  box-shadow: 0px 0px 5px #888;
}
.footer .controls {
  width: 30%;
  margin-right: 2%;
  text-align: center;
}
.footer .ui-slider {
  margin-right: 2%;
  width: 30%;
}


